import React, { useEffect, useState } from 'react'
import editIcon from "../../Media/editIconBlue.png"
import messageIcon from "../../Media/chatIconBlue.png"
import { randomBannerImage, readableJson, userIDToName } from '../../Functions'

function Profile({userID}) {

    const [showProfileImageWindow, setShowProfileImageWindow] = useState(false)
    const [viewSection, setViewSection] = useState("About")
    const [editMode, setEditMode] = useState(false)
    const [bannerImageURL, setBannerImageURL] = useState(randomBannerImage())

    const allUsers = {}
    const isMobile = true
    const isAnon = true
    const adminData = true

    function signOutFunction(){

    }

    function profileSectionDisplay(){
        if(viewSection === "About" ){
          return (
            <>
              {isMobile ?
                <>
                  <div className='profileTileColumn profileTileLeftColumn profileTileColumnMobile'>
  
                    <div className='profileTile'>
                      <div className='profileTileTitle'>
                          Pictures
                      </div>
                      {/* {editMode ? 
                        <ProfileImages userIDOverride={profileID}></ProfileImages>
                        :
                        <ProfileViewImages ProfileViewImages profileData={profileData}></ProfileViewImages>
                      } */}
                    </div>
                    
                  </div>
                  <div className='profileTileColumn profileTileRightColumn profileTileColumnMobile'>
  
                    <div className='profileTile'>
                      <div className='profileTileTitle'>
                        Bio
                      </div>
                      <div className='profileTileInner'>
                        {editMode?
                        <div className='editProfilePageBio'>
                          {/* <ProfileEditInputField attributeKey={"bio"} attributeName={"Bio"} userIDOverride={profileID} textarea></ProfileEditInputField> */}
                        </div>
                        // <textarea defaultValue={profileData?.bio} className='editProfilePageBio'>
  
                        // </textarea>
                        :
                        <pre>
                          {profileData?.bio}
                        </pre>
                        }
                      </div>
                    </div>
  
                    <div className='profileTile'>
                      <div className='profileTileTitle'>
                        Extended Info
                      </div>
                      <div className='profileTileInner'>
                        {/* {textDisplayFields.map(textDisplayObject => (
                          textDisplayField(textDisplayObject)
                        ))} */}
                      </div>
                    </div>
                    <div>
                      {/* {"isAnon: "+isAnon+" profileID: "+profileID+" userID: "+userID} */}
                    </div>
                    {isAnon && (!profileID || (profileID === userID)) &&
                        <div className='profileTile'>
                          <div className='flexBox'>
                              {/* <CreateAccountInputs></CreateAccountInputs> */}
                          </div>
                        </div>
                    }
  
  
                  </div>
                </>
                :
                <>
                  <div className='profileTileColumn profileTileLeftColumn'>
                    <div className='profileTile'>
                      <div className='profileTileTitle'>
                          Pictures
                      </div>
                      {/* {editMode ? 
                        <ProfileImages userIDOverride={profileID}></ProfileImages>
                        :
                        <ProfileViewImages ProfileViewImages profileData={profileData}></ProfileViewImages>
                      } */}
                    </div>
                    
                  </div>
                  <div className='profileTileColumn profileTileRightColumn'>
  
                    <div className='profileTile'>
                      <div className='profileTileTitle'>
                        Bio
                      </div>
                      <div className='profileTileInner'>
                        {editMode?
                        <div className='editProfilePageBio'>
                          {/* <ProfileEditInputField attributeKey={"bio"} attributeName={"Bio"} userIDOverride={profileID} textarea></ProfileEditInputField> */}
                        </div>
                        // <textarea defaultValue={profileData?.bio} className='editProfilePageBio'>
  
                        // </textarea>
                        :
                        <pre>
                          {profileData?.bio}
                        </pre>
                        }
                      </div>
                    </div>
  
                    <div className='profileTile'>
                      <div className='profileTileTitle'>
                        Extended Info
                      </div>
                      <div className='profileTileInner'>
                        {/* {textDisplayFields.map(textDisplayObject => (
                          textDisplayField(textDisplayObject)
                        ))} */}
                      </div>
                    </div>
                  </div>
                </>
              }
            </>
          )
        }
        if(viewSection === "Message" ){
          return (
            <div className='profilePageChatContainerOuter'>
              <div className='profileTile profilePageChatContainer'>
                {/* <Chat otherUserIDOverride={profileID}></Chat> */}
  
              </div>
            </div>
          )
        }
        if(viewSection === "Pictures" ){
          return(
            <></>
            // <ProfileViewImages profileData={profileData}></ProfileViewImages>
          )
        }
        if(viewSection === "About" ){
  
        }
        // Pull users current app state from admin data and display it here, parse it a bit, also can put chat menu here
        if(viewSection === "Admin" ){
          let userApplicationState = adminData?.users?.[profileID]?.currentApplicationState
          return(
            <>
              {/* <AdminApplicationView userApplicationState={userApplicationState}></AdminApplicationView> */}
              <pre>
                <div>
                  {"Viewing User: "+userIDToName(allUsers, userApplicationState?.viewingUserID)}
                </div>
                <div>
                  {"Chatting With: "+userIDToName(allUsers, userApplicationState?.chatUserID)}
                </div>
                {readableJson(userApplicationState)}
              </pre>
            </>
          )
        }
        if(viewSection === "AccountOptions" ){
          return(
            <div  className='profileTileColumn profileTileColumnMobile'>
              <div className='profileTile'>
                <button className='accountOptionsButton'>Change Password</button>
                <button className='accountOptionsButton'>Change Email</button>
                <button onClick={signOutFunction} className='accountOptionsButton'>Sign Out</button>
              </div>
            </div>
          )
        }
      }
    const profileData = {}
    const profileID = ""



  return (
    <div className='profile'>
        {/* {showProfileImageWindow && <ImageViewer startingIndex={0} imageUrlArray={[profileData?.images?.main?.downloadURL]} close={()=>setShowProfileImageWindow(false)}></ImageViewer>} */}
        <div className='pageTop'>
            {/* Banner */}
            <div className='profileBanner'>
                {editMode?
                    <div></div>
                    // <ImageUploader 
                    //     imageIdentifier={"banner"}
                    //     dataPath={"freeFer/users/" + profileID + "/profileData/images/"+"banner"}
                    //     imagePath={"freefer/users/" + profileID + "/"}
                    //     imagePathObject={allUsers[profileID]?.profileData?.images?.banner}
                    // ></ImageUploader> 
                    :
                    <img src={profileData?.images?.banner?.downloadURL || bannerImageURL}></img>
                }
            </div>
            <div className='profileTopBar'>
                <div className='profileMainImage' onClick={()=>setShowProfileImageWindow(true)}>
                <img src={profileData?.images?.main?.downloadURL}></img>
                </div>
                <div className='profileName'>
                    {profileData?.name || "Test Name"}
                {/* <div>
                    {"profileID: "+profileID}
                </div>
                <div>
                    {"userID: "+userID}
                </div> */}
                </div>
                {(userID === profileID || userID === "v9euCYVZG6MtMdx6JqD0SXtDmD53") && 
                    <>
                        <div className='imageButtonPosition imageButtonSize' onClick={()=>setEditMode(!editMode)}>
                            <div className='imageButtonImage2'>
                                <img src={editIcon}></img>
                            </div>
                            <div className='imageButtonLabel'>{editMode ? "Stop Editing" : "Edit Profile"}</div>
                        </div>
                    </>
                }
                <div className='profileButtons'>
                    <div 
                        className={'profileButton '+(viewSection === "About" ? "profileButtonSelected":"")} 
                        onClick={()=>setViewSection("About")}
                    >
                        About
                    </div>

                    {profileData?.userType === "donor" &&
                        <div 
                            className={'profileButton '+(viewSection === "Screening" ? "profileButtonSelected":"")} 
                            onClick={()=>setViewSection("Screening")}
                        >
                            Screening
                        </div>
                    }

                    {profileID !== userID &&
                        <div 
                            className={'profileButton '+(viewSection === "Message" ? "profileButtonSelected":"")} 
                            onClick={()=>setViewSection("Message")}
                        >
                            <div className='profileButtonText'>
                                Message
                            </div>
                            <div className='smallIcon'>
                                <img src={messageIcon}></img>
                            </div>
                        </div>
                    }
                    {profileID === userID &&
                        <div 
                            className={'profileButton '+(viewSection === "AccountOptions" ? "profileButtonSelected":"")} 
                            onClick={()=>setViewSection("AccountOptions")}
                        >
                            Account Options
                        </div>
                    }
                    {userID === "v9euCYVZG6MtMdx6JqD0SXtDmD53" &&
                        <div 
                            className={'profileButton '+(viewSection === "Admin" ? "profileButtonSelected":"")} 
                            onClick={()=>setViewSection("Admin")}
                        >
                            Admin
                        </div>
                    }
                </div>
            </div>
        </div>
        <div className='profileBottom'>
            <div className='profileBottomInner profileBottomInnerMobile'>
                {profileSectionDisplay()}
            </div>
        </div>
    </div>
  )
}

export default Profile