import React, { useState } from 'react'
import "./Navbar.css"
import chatIcon from "../../Media/chatIconInverted.png"
import acountIcon from "../../Media/accountIconInverted.png"
import menuIcon from "../../Media/menuIconInverted.png"
import searchIcon from "../../Media/searchIconInverted.png"
import { Link } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { setShowChat, setShowMenu, toggleShowChat, toggleShowMenu } from '../../GlobalState/AppSlice'


function Navbar({top, color}) {

  const dispatcher = useDispatch()

  const [navBarTop, setNavBarTop] = useState(false)

  const mainProfileImage = ""
  const newMessageCircleLarge = ""
  const userID = ""
  const unreadMessages = 0

  return (
    <div className={'navbar '+(top ? "navbarTop":"")}>
      {/* Account */}
      <div>
        <Link to={"/profile/"+userID} title="Account" className='topNavButton'>
          {mainProfileImage ? 
            <img src={mainProfileImage} className='topNavLinkImg roundImage' style={{backgroundColor: color}}></img>
            :
            <img src={acountIcon} style={{backgroundColor: color}}></img>              
          }
        </Link>   
      </div> 


      {/* Menu */}
      <div title="Menu" onClick={()=>dispatcher(toggleShowMenu())} className='topNavButton'>
        <img src={menuIcon} style={{backgroundColor: color}}></img>
      </div> 

      {/* Search */}     
      <div>
        <Link to={"/search"} title="Donor Search" className='topNavButton'>
            <img src={searchIcon} style={{backgroundColor: color}}></img>
        </Link>   
      </div> 

      {/* Chats */}
      <div title="Chats" className='topNavButton'>
        <img src={chatIcon} onClick={()=>dispatcher(toggleShowChat())} style={{backgroundColor: color}}></img>
        {unreadMessages != 0 && <div className={'newMessageCircle ' +(newMessageCircleLarge ? "newMessageCircleLarge":"")}>{unreadMessages}</div>}
      </div> 
    </div>
  )
}

Navbar.defaultProps = {
  color: "#0758ff"

}
export default Navbar