  export function readableJson(json){
    if(!json || typeof json !== "object") return ""
    let jsonString = JSON.stringify(json)
    jsonString = jsonString.replaceAll(",",",\n  ")
    jsonString = jsonString.replaceAll("{","{\n  ")
    jsonString = jsonString.replaceAll("}",",\n}")
    return jsonString
  }
