import { useEffect, useState } from 'react';
import './App.css';
import Home from './Features/Home/Home';
import Navbar from './Features/NavBar/Navbar';
import TestComponent from './TestComponent';
import Search from './Features/Search/Search';
import ProfileView from './Features/Profile/ProfileView';
import { HashRouter, Route, Routes } from 'react-router-dom';
import Menu from './Features/Menu/Menu';
import LoadData from './Features/DB/LoadData';
import ProfilePage from './Features/Profile/ProfilePage';

function App() {

  // ========================================
  // #region adjust for mobile keyboard height 

  useEffect(()=>{
    getHeights()
    resizeListener()
  },[])

  const [height, setHeight] = useState()
  const [viewportHeight, setViewportHeight] = useState()
  // Want to set the height of the chat window so the top is always showing
  function resizeListener(){
    window.addEventListener("resize", getHeights)
  }
  function getHeights(event){
    // console.log(window.innerHeight)
    setHeight(window.innerHeight)
    // console.log(window.visualViewport.height)
    setViewportHeight(window.visualViewport.height)
  }

  // #endregion adjust for mobile keyboard height 

  return (
    // <div className="App" style={{height: "calc(100% - "+(height - viewportHeight)+"px)"}}>
    <div className="App">
      {/* This sets a smaller window for dev purposes */}
      <div className='appInner'>
        <HashRouter>
          <LoadData></LoadData>
          <Menu></Menu>
          <Routes>
            <Route path='/' element={<Home></Home>}></Route>
            <Route path='/search' element={<Search></Search>}></Route>
            <Route path='/profile/:profileID' element={<ProfilePage></ProfilePage>}></Route>
          </Routes>
        </HashRouter>
      </div>
    </div>
  );
}

export default App;
