import { onValue, ref, set } from '@firebase/database'
import React, { useEffect, useRef } from 'react'
import { auth, setUserID, loadAnonUser, setOldUserID, db, setAllUsers, setProfileData, setChats, updateUserData, setIsMobile, setUserData } from '../../GlobalState/AppSlice'
import { useDispatch, useSelector } from 'react-redux'
import { isMobile } from 'react-device-detect'
import { onAuthStateChanged } from '@firebase/auth'
import { dateString } from '../../Functions'

function LoadData() {
    const userID = useSelector(state => state.appSlice.userID)
    const dispatcher = useDispatch()

    useEffect(()=>{
        // Load all user data on start
        loadUsers()
        // Sets the users's profileData.lastActive = current datatime on start
        setActiveDateTime()
    },[])

    useEffect(()=>{
        dispatcher(setIsMobile(isMobile))
    },[isMobile])

    // Creates loadProfileData and loadChats listeners when the userID is updated
    useEffect(()=>{
        // If the user ID has not loaded there is no user to load data from
        if(!userID) return
        loadProfileData()
        loadChats()
        // This is for desktop view
        //loadOpenChats()
    },[userID])

    /**
     * Loads the data for all users. Could seperate this later to just the data other users would need to interact with them like profileData, not their chats or activity data
    */
    function loadUsers(){
        onValue(ref(db, "freeFer/users"), snap => {
            setTimeout(() => {        
                dispatcher(setAllUsers(snap.val()))
            }, 50);
        })
    }

    /**
     * Creates on value listener for the user's profile data 
    */
    function loadProfileData(){
        if(!userID) return
        onValue(ref(db, "freeFer/users/"+userID), snap => {
            setTimeout(() => {        
                dispatcher(setUserData(snap.val()))
                dispatcher(setProfileData(snap.val()?.profileData))
            }, 50);
        })
    }

    /**
     * Creates on value listener for the user's chats 
    */ 
    function loadChats(){
        if(!userID) return
        onValue(ref(db, "freeFer/users/"+userID+"/chats"), snap => {
            setTimeout(() => {        
                dispatcher(setChats(snap.val()))
            }, 50);
        })
    }

    /**
     * Sets the users's profileData.lastActive = current datatime in the db
    */
    function setActiveDateTime(){
        let newObject = {}
        newObject.ID = userID
        newObject.lastActive = dateString()
        dispatcher(updateUserData(newObject))
    }
    
    function loadOpenChats(){
        if(!userID) return
        onValue(ref(db, "freeFer/users/"+userID+"/openChatUserIDs"), snap => {
            let otherUserIDsObject = snap.val()
            // console.log("loaded otherUserIDsObject")
            // console.log(otherUserIDsObject)

            if(!otherUserIDsObject || typeof otherUserIDsObject !== "object"){
                // console.log("invalid open chats object:")
                // console.log(otherUserIDsObject)
                setTimeout(() => {
                    // dispatcher(setOpenChatUserIDsDB([]))
                }, 50)

                return
            }
            
            let tempOpenChatOtherUserIDsArray = []
            Object.entries(otherUserIDsObject).forEach(([otherUserID, floatingChatData]) => {
            tempOpenChatOtherUserIDsArray.push(otherUserID)
            })
            // console.log("tempOpenChatOtherUserIDsArray")
            // console.log(tempOpenChatOtherUserIDsArray)
            // setOpenChatUserIDs(tempOpenChatOtherUserIDsArray)
            setTimeout(() => {
                // dispatcher(setOpenChatUserIDsDB(tempOpenChatOtherUserIDsArray))
            }, 50);
        })
    }

    // Auth Stuff (logging in, transfering form anon to regular user)

    const allUsers = useSelector(state => state.appSlice.allUsers)
    const oldUserID = useSelector(state => state.appSlice.oldUserID)

    const oldUserIDRef = useRef()
    const allUsersRef = useRef()


    useEffect(()=>{
      startAuth()
    },[])

    useEffect(()=>{
      oldUserIDRef.current = oldUserID
    },[oldUserID])
    useEffect(()=>{
      allUsersRef.current = allUsers
    },[allUsers])

    /**
     * Looks for an account, if there is one saves the userID, if not loads or creates an anonomous user ID
     */
    function startAuth(){
      onAuthStateChanged(auth, user => {
        if(user){
          if(oldUserIDRef.current){

            // Get the anon uses's data to put into the new account
            let oldUserData = allUsersRef.current[oldUserIDRef.current]
            
            // Keep track of the old user ID
            if(oldUserData && typeof oldUserData === "object")
              oldUserData.oldUserID = oldUserIDRef.current

            // Put the anon user data in the newly created users's data
            set(ref(db, "freeFer/users/"+user.uid), oldUserData)
            
            // Remove that anon user ID so the profile data is no longer there
            window.localStorage.removeItem("anonUserID")

            // Remove the old user ID so this only happens once
            dispatcher(setOldUserID())

            // Creating a new user happens from the profile page so go there with the new user ID
            window.location.href = "http://localhost:3000/#/profile/"+user?.uid

          }

          // Set the global userID based on the user that is signed in
          dispatcher(setUserID(user.uid))
        }
        else
          // If there is no user signed in load or create an anomous user ID
          dispatcher(loadAnonUser())
      })
    }


    // Old function that loads the profile data and chats from the userID string and allUsers object
    // function loadProfileData(){
    //     if(!userID || !allUsers) return
    //     let tempUserData = allUsers[userID]
    //     if(!tempUserData) return
    //     dispatcher(setProfileData(tempUserData.profileData))
    //     dispatcher(setChats(tempUserData.chats))
    // }


  return (
    <></>
  )
}

export default LoadData