import React, { useState } from 'react'
import Filter from './Filter'
import PagedUsers from './PagedUsersComponent'
import "./Search.css"
import Navbar from '../NavBar/Navbar'
import { useDispatch, useSelector } from 'react-redux'
import ProfileView from '../Profile/ProfileView'
import PagedUsersComponent from './PagedUsersComponent'
import { setViewProfileID } from '../../GlobalState/AppSlice'

function Search() {

  const allUsers = useSelector(state => state.appSlice.allUsers)
  const viewProfileID = useSelector(state => state.appSlice.viewProfileID)
  const dispatcher = useDispatch()

    const [filteredUsers, setFilteredUsers] = useState({})
    function setFilteredObject(filteredObject){
      setFilteredUsers(filteredObject)
    }

    function selectUser(userID){
      dispatcher(setViewProfileID(userID))
    }

  return (
    <>
      <Navbar></Navbar>

      {viewProfileID && <ProfileView></ProfileView>}

      <div className='searchPage'>

          <Filter setFilteredObject={setFilteredObject} object={allUsers}></Filter>

          <div className='searchResultsTopBar'>Search</div>

          <div className='appScroller'>
              <PagedUsersComponent selectUser={selectUser} usersObject={filteredUsers}></PagedUsersComponent>
          </div>

      </div>
    </>
  )
}

export default Search