import React, { useEffect, useState } from 'react'
import UserTile from './UserTile'
import { useSelector } from 'react-redux'
import "./PagedUsers.css"

function PagedUsersComponent({usersObject, selectUser}) {
    const isMobile = useSelector(state => state.appSlice.isMobile)

    const [pageRange, setPageRange] = useState([0, isMobile ? 8 : 12])
    const [pagedUsers, setPagedUsers] = useState({})
    const [count, setCount] = useState(0)
    
    useEffect(()=>{
        pageUsers()
        setPageRangeValues()
    },[usersObject, pageRange])

    function pageUsers(){
        if(typeof usersObject !== "object") return

        // Collect the users within the range of the pager
        let tempPagedUsersObject = {}
        let count = 0
        Object.entries(usersObject).forEach(([id, data], index) => {
            count++
            if(index >= pageRange[0] && index < pageRange[1])
                tempPagedUsersObject[id] = data

        })
        setCount(count)
        setPagedUsers(tempPagedUsersObject)
    }
    function setPageRangeValues(){
        let startInput = document.getElementById("pageRangeStart")
        let endInput = document.getElementById("pageRangeEnd")
        if(startInput)
            startInput.value = pageRange[0]
        if(endInput)
            endInput.value = pageRange[1]
    }

    function pageNext(){
        let diff = pageRange[1] - pageRange[0]
        let newStart = pageRange[0] + diff
        let newEnd = pageRange[1] + diff
        if(newStart >= count)
            return
        setPageRange([newStart, newEnd])
        //setPageRangeValues()
    }
    function pageBack(){
        let diff = pageRange[1] - pageRange[0]
        let newStart = pageRange[0] - diff
        let newEnd = pageRange[1] - diff
        if(newStart < 0){
            newStart = 0
            newEnd = diff
        }
        setPageRange([newStart, newEnd])
    }
    function toStart(){
        setPageRange([0, 12])
    }
    function updatePageRange(){
        let start = document.getElementById("pageRangeStart").value
        let end = document.getElementById("pageRangeEnd").value
        try{
            start = parseInt(start)
            end = parseInt(end)

            // For invalid inputs
            if(isNaN(start))
                start = pageRange[0]
            if(isNaN(end))
                end = pageRange[1]
                
            if(start == pageRange[0] && end == pageRange[1]) return

            setPageRange([start, end])
        }catch(e){
            console.log("invalid page range input")
        }
    }
    return (
        <div className='pagedUsersContainer'>
            <div className='pagerButtons' title={'Showing '+pageRange[0]+' to '+ pageRange[1]+' of ' + count + ' results'}>
                <div>
                    <div title="Back" onClick={pageBack} className='rangeButton'>{"<"}</div>
                    {pageRange[0] + "-" + pageRange[1] + " of " + count}
                    <div title="Next" onClick={pageNext} className='rangeButton'>{">"}</div>
                </div>
            </div>
        
            {Object.entries(pagedUsers).map(([otherUserID, userData]) => (
                <>
                {console.log(userData)}
                    <UserTile userID={otherUserID} profileData={userData.profileData} selectUser={selectUser} key={otherUserID}></UserTile>
                </>
            ))}
            
        </div>
    )
}

PagedUsersComponent.defaultProps = {
    userSelected: (userID)=>{console.log("User Selected: "+userID)},
}

export default PagedUsersComponent