import React from 'react'
import { useDispatch, useSelector } from 'react-redux'

function UserTile({userID, profileData, selectUser}) {
  const isMobile = useSelector(state => state.appSlice.isMobile)

  const dispatcher = useDispatch()

  function selectUserLocal(userID){
    
    // Call the function provided by the propf
    selectUser(userID)
  
    // Push the user action showing that the user is viewing a profile 
    // dispatcher(pushUserAction({type:"viewProfile", otherUserID: userID}))

  }

  return (
    <>
      {isMobile ?
        <div className='userTile userTileMobile' onClick={()=>selectUserLocal(userID)} key={userID}> 
            <div className='userTileImage'>
              <img src={profileData?.images?.main?.downloadURL}></img>
              {profileData?.activeSessions && 
                <div className='activeBanner'>
                  <div className='activeBannerCircle'></div>
                  <div className='activeBannerCircleText'>
                    Active Now
                  </div>
                </div>
              }
            </div>
            <div className='userTileText'>              
              <div>{profileData?.name + " " + (profileData?.age || "")}</div>
              <div>{(profileData?.state || "") + " " + (profileData?.country || "")}</div>
            </div>                  
        </div>
        :
        <div className='userTile' onClick={()=>selectUser(userID)} key={userID}> 
            <div className='userTileImage'>
            <img src={profileData?.images?.main?.downloadURL}></img>
            </div>
            <div className='userTileText'>
            <div>{profileData?.name + " " + (profileData?.age || "")}</div>
            <div>{profileData?.location}</div>
            </div>                  
        </div>
      }
    </>
  )
}

UserTile.defaultProps = {
  selectUser: (userID) => {console.log("Selected user: "+userID)},
}

export default UserTile