import React from 'react'
import { setProfilePageWindowUserID, setShowChat, setShowChatSelectorWindow, setShowChatsWindow, setShowMenu, setViewProfileID, syncAppState, toggleShowChat, toggleShowChatSelectorWindow } from '../../GlobalState/AppSlice'
import { useDispatch, useSelector } from 'react-redux'
import "./Menu.css"
import { Link } from 'react-router-dom'

function Menu() {
    const isMobile = useSelector(state => state.appSlice.isMobile)
    const showMenu = useSelector(state => state.appSlice.showMenu)
    const userID = useSelector(state => state.appSlice.userID)
    const dispatcher = useDispatch()

    function closeMenu(e, dontCloseSearchProfile){
      console.log("closeMenu dontCloseSearchProfile: "+dontCloseSearchProfile)
      dispatcher(setShowMenu(false))
      if(!dontCloseSearchProfile)
        dispatcher(setViewProfileID())
    }

  return (
    <>
    
      {showMenu &&
          <div className='menuWindow'>
              <div>
                <div className='menuTitle'>
                  Menu
                </div>
                <div className='closeButton' onClick={closeMenu}>x</div>
              </div>

              <div className='menuButton' onClick={closeMenu}>
                <Link to={"/search"} onClick={()=>dispatcher(syncAppState())}>  
                  Donor Search
                </Link>
              </div>
              <div className='menuButton' onClick={(e)=>{dispatcher(setShowChat(true)); closeMenu(e, true);}}>
                Chats
              </div>
              <div className='menuButton' onClick={closeMenu}>
                <Link to={"/profile/"+userID}>  
                  Profile
                </Link>
              </div>
              <div className='menuButton' onClick={closeMenu}>
                <Link to={"/"}>
                  Home
                </Link>
              </div>
          </div>
      }
    </>
  )
}

export default Menu