import React from 'react'

function CircleInfoBox({image, title, content}) {
  return (
    <div className={'circleInfoBox '}>
        <img src={image}></img>
        <div className={'circleInfoTextBox'}>
            <h2 className='circleInfoTitle'>{title}</h2>
            <p className='circleInfoContent'>{content}</p>
        </div>
    </div>
  )
}

export default CircleInfoBox